.form{
  &-header{
    margin-bottom: 30px;
    text-align: center;
    @include max(sm){
      margin-bottom: 20px;
    }
    .title{
      &_md{
        color: $green-dark;
      }
      &_xs{
        margin-top: 5px;
      }
    }
  }
  &-body{
    text-align: center;
  }

  &-group {
    position: relative;
    margin-bottom: 10px;
  }
  &-control {
    display: block;
    width: 100%;
    height: auto;
    padding: 10px 28px;
    background: #fff 28px 12px / 20px 20px no-repeat;
    border: 1px solid #c4c4c4;
    border-radius: 20px;
    color: $black;
    text-align: left;
    box-shadow: none !important;
    outline: none !important;
    transition: $transition;
    @include max(sm){
      padding: 10px 30px;
      // background-position: 9px 13px;
      // background-size: 16px 16px;
    }
    // &[name="name"]{
    //   background-image: url(../img/icons/input_name.png);
    // }
    // &[name="tel"]{
    //   background-image: url(../img/icons/input_tel.png);
    // }
    // &[name="email"]{
    //   background-image: url(../img/icons/input_email.png);
    // }
    // &[name="message"]{
    //   background-image: url(../img/icons/input_message.png);
    // }
    &__textarea{
      height: 75px;
      resize: none;
    }
    &::placeholder {
      color: #9f9f9f;
      opacity: 1;
      transition: opacity $transition;
    }
    &:focus {
      border-color: $green-light;
      &::placeholder {
        color: $black;
        opacity: 0;
        transition: opacity $transition;
      }
    }
  }
  .button{
    min-width: 280px;
    margin-top: 30px;
    padding: 14px 40px 15px;
    font-size: 18px;
    @include max(sm){
      min-width: 260px;
      margin-top: 10px;
      padding: 9px 40px 10px;
    }
    @include max(xs){
      min-width: 248px;
      margin-top: 0;
    }
  }
}

.policy {
  margin-top: 20px;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #C1C1C1;
  a{
    color: #C1C1C1;
    text-decoration: underline;
    &:hover{
      color: #C1C1C1;
      text-decoration: none;
    }
  }
  + .button{
    margin-top: 20px;
  }
}