.footer{
  position: relative;
  padding: 20px 0;
  background: #fff;
  font-weight: 300;
  z-index: 4;
  @include max(md){
    padding: 15px 0;
  }
  .row{
    display: flex;
    @include max(md){
      flex-direction: column;
    }
  }
  a{
    color: $black;
    &:hover{
      color: $green-light;
    }
  }
  &__copy{
    @include min(md){
      width: 33.33333%;
    }
    @include max(md){
      margin: 0 0 10px;
      text-align: center;
    }
    > span {
      display: block;
    }
  }
  &__social{
    @include min(md){
      width: 58.33333%;
      .social{
        margin: 0 136px;
      }
    }
    @include max(md){
      margin: 0 0 10px;
      .social{
        justify-content: center;
      }
    }
  }
  &__contacts{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    width: 25%;
    @include max(md){
      align-items: center;
      width: 100%;
    }
    a[href^="mailto"]{
      position: relative;
      padding-left: 35px;
      text-decoration: none;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        top: 2px;
        display: block;
        width: 21px;
        height: 15px;
        background: url(../img/icons/icon_email.png) center no-repeat;
      }
    }
  }
}