.screen{
  position: relative;
  z-index: 0;
  .container{
    z-index: 3;
    > .row{
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
      &:before,
      &:after{
        display: none;
      }
    }
  }
  &__bg{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  &__overlay{
    position: absolute;
    left: 50%;
    top: 0;
    bottom: 0;
    transform: translateX(-50%);
    width: 1920px;
    overflow: hidden;
  }
}
// Первый экран
.section-main{
  background: #f6f6f6;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    // bottom: -265px;
    bottom: -39%;
    display: block;
    // width: 468px;
    width: 24.6%;
    // height: 440px;
    height: 64.5%;
    background: url(../img/laser.png) center / contain no-repeat;
    z-index: 3;
    pointer-events: none;
    @include max(md){
      display: none;
    }
  }
  &__bg{
    background: url(../img/background/bg__screen_main.jpg) center / cover no-repeat;
    opacity: .15;
    z-index: 2;
  }
  &__overlay{
    z-index: 1;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
      border-radius: 50%;
    }
    &:before{
      left: -534px;
      width: 711px;
      height: 711px;
    }
    &:after{
      right: -177px;
      width: 961px;
      height: 961px;
      @include max(md){
        right: auto;
        top: auto;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 100vw;
        height: 135px;
        border-radius: 0;
      }
    }
  }
  .container{
    padding-top: 165px;
    padding-bottom: 165px;
    @include max(lg){
      padding-top: 100px;
      padding-bottom: 100px;
    }
    @include max(md){
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
  &._franchise{
    &:before{
      display: none;
    }
    .container{
      padding-top: 85px;
      padding-bottom: 130px;
      @include max(md){
        padding-top: 50px;
        padding-bottom: 50px;
      }
    }
    @include max(md){
      .section-main__overlay:after{
        height: 185px;
      }
    }
  }
}
.main{
  &__left{
    width: 50%;
    @include max(md){
      width: 100%;
      text-align: center;
    }
    &._franchise{
      width: 58.33333%;
      @include max(md){
        width: 100%;
      }
    }
    .title{
      margin-top: 20px;
      &._franchise{
        margin-top: 35px;
        span{
          display: block;
        }
      }
    }
    .description{
      min-height: 80px;
      margin-top: 20px;
      font-size: 25px;
      &._franchise{
        min-height: auto;
        line-height: 1;
      }
      @include max(lg){
        font-size: 20px;
      }
      @include max(md){
        min-height: auto;
      }
    }
    .button{
      margin-top: 20px;
      font-size: 18px;
      padding-top: 14px;
      padding-bottom: 15px;
      &._franchise{
        width: 360px;
        margin-top: 60px;
        @include max(md){
          width: 290px;
          margin-top: 10px;
        }
      }
    }
  }
  &__right{
    display: flex;
    flex-wrap: wrap;
    width: 50%;
    @include max(md){
      width: 100%;
    }
  }
  &-advantage{
    width: 50%;
    transition-duration: 0s;
    @include max(md){
      display: flex;
      flex-wrap: wrap;
      width: calc(100% + 30px);
      margin: 50px -15px 0;
    }
    &[data-aos]{
      .main-advantage__item{
        opacity: 0;
        transform: scale(1.2);
        transition: opacity 1s ease, transform 1s ease;
      }
      &.aos-animate{
        .main-advantage__item{
          opacity: 1;
          transform: scale(1);
          &:nth-child(1){
            transition-delay: .5s;
          }
          &:nth-child(2){
            transition-delay: .65s;
          }
          &:nth-child(3){
            transition-delay: .8s;
          }
        }
      }
    }
    &__item{
      position: relative;
      display: flex;
      align-items: center;
      width: 190px;
      margin-bottom: 68px;
      @include min(md){
        &:nth-child(1){
          right: -40px;
          width: 175px;
        }
        // &:nth-child(2){}
        &:nth-child(3){
          right: -50px;
          width: 160px;
        }
      }
      @include max(md){
        flex-direction: column;
        width: 33.33333%;
        margin-bottom: 0;
        padding: 0 10px;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
    &__image{
      order: 2;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 72px;
      height: 72px;
      background: #fff;
      box-shadow: 0px 23px 60px 0px rgba(0, 0, 0, 0.24);
      border-radius: 50%;
      @include max(md){
        order: 1;
        margin-bottom: 10px;
      }
      &:before{
        content: '';
        position: absolute;
        left: -6px;
        right: -6px;
        top: -6px;
        bottom: -6px;
        border: 6px solid #399d7d;
        border-radius: inherit;
        z-index: -1;
      }
      img{
        max-width: 90%;
        max-height: 90%;
      }
    }
    &__descr{
      order: 1;
      width: calc(100% - 72px);
      padding-right: 20px;
      font-weight: 300;
      font-size: 18px;
      @include max(md){
        order: 2;
        width: 100%;
        min-height: 38px;
        padding: 0;
        font-weight: 400;
        font-size: 16px;
        color: #fff;
        text-align: center;
      }
    }
  }
  &-advantage-row{
    display: inline-flex;
    align-items: flex-start;
    width: 100%;
    margin-top: 45px;
    transition-duration: 0s;
    @include max(md){
      display: flex;
      flex-wrap: wrap;
      width: calc(100% + 30px);
      margin: 50px -15px 0;
    }
    &[data-aos]{
      .main-advantage-row__item{
        opacity: 0;
        transform: scale(1.2);
        transition: opacity 1s ease, transform 1s ease;
      }
      &.aos-animate{
        .main-advantage-row__item{
          opacity: 1;
          transform: scale(1);
          &:nth-child(1){
            transition-delay: .5s;
          }
          &:nth-child(2){
            transition-delay: .65s;
          }
          &:nth-child(3){
            transition-delay: .8s;
          }
        }
      }
    }
    &__item{
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: auto;
      margin-right: 60px;
      &:last-child{
        margin-right: 0;
      }
      @include max(md){
        flex-direction: column;
        width: 33.33333%;
        margin-right: 0;
        padding: 0 10px;
      }
    }
    &__image{
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 50px;
      margin-bottom: 10px;
      background: #fff;
      box-shadow: 0px 23px 60px 0px rgba(0, 0, 0, 0.24);
      border-radius: 50%;
      &:before{
        content: '';
        position: absolute;
        top: -3px;
        right: -3px;
        bottom: -3px;
        left: -3px;
        border: 3px solid #399d7d;
        border-radius: inherit;
        z-index: -1;
      }
      &:after{
        content: '';
        position: absolute;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        border: 1px solid #399d7d;
        border-radius: inherit;
        animation: main-advantage-row__image 2s ease infinite;
        pointer-events: none;
        opacity: 0;
      }
      img{
        max-width: 90%;
        max-height: 90%;
      }
    }
    &__descr{
      width: 100%;
      font-weight: 300;
      font-size: 18px;
      text-align: center;
      @include max(md){
        width: 100%;
        min-height: 38px;
        padding: 0;
        font-weight: 400;
        font-size: 16px;
        color: #fff;
      }
    }
  }
  &-device{
    position: absolute;
    bottom: 0;
    right: -145px;
    &._franchise{
      right: -275px;
    }
    @include max(lg){
      right: -12%;
    }
    @include max(md){
      display: none;
    }
    &__image{
      position: relative;
      display: block;
      @include max(lg){
        width: 400px;
      }
    }
  }
}
@keyframes main-advantage-row__image{
  0%{
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    opacity: 1;
  }
  100%{
    top: -12px;
    right: -12px;
    bottom: -12px;
    left: -12px;
    opacity: 0;
  }
}
.scroll-down{
  position: absolute;
  bottom: 39px;
  left: 50%;
  transform: translateX(-50%);
  @include max(md){
    display: none;
  }
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 20px;
    height: 9px;
    background: url(../img/scroll-down_arrow.png) center / contain no-repeat;
  }
  &:before{
    bottom: -14px;
    animation: scroll-down_before 1.25s ease .5s infinite;
  }
  &:after{
    bottom: -19px;
    animation: scroll-down_after 1.25s ease .5s infinite;
  }
  img{
    display: block;
  }
}
@keyframes scroll-down_before {
  0% {
    bottom: -9px;
    opacity: 0;
  }
  30%{
    opacity: 1;
  }
  100%{
    bottom: -14px;
    opacity: 0;
  }
}
@keyframes scroll-down_after {
  0% {
    bottom: -9px;
    opacity: 0;
  }
  30%{
    opacity: 1;
  }
  80%{
    opacity: 0;
  }
  100%{
    bottom: -19px;
    opacity: 0;
  }
}

