}
.gradient-circle{
  position: relative;
  background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
  border-radius: 50%;
  &:after{
    content: '';
    position: absolute;
    top: 20px;
    right: 20px;
    bottom: 20px;
    left: 20px;
    background: #fff;
    border-radius: inherit;
  }
  &__animation{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
    border-radius: 50%;
    animation: gradient-circle 2s ease infinite;
    z-index: -1;
    pointer-events: none;
    &:after{
      content: '';
      position: absolute;
      background: #fff;
      border-radius: inherit;
      animation: gradient-circle_after 2s ease infinite;
    }
    // &_1{
    //   &:after{
    //   }
    // }
    &_2{
      animation-delay: .5s;
      &:after{
        animation-delay: .5s;
      }
    }
  }
}
@keyframes gradient-circle{
  0%{
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 1;
  }
  100%{
    top: -20px;
    right: -20px;
    bottom: -20px;
    left: -20px;
    opacity: 0;
  }
}
@keyframes gradient-circle_after{
  0%{
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
  }
  100%{
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}
@keyframes gradient-circle_rent{
  0%{
    top: -4px;
    right: -4px;
    bottom: -4px;
    left: -4px;
    opacity: 0.05;
  }
  100%{
    top: -20px;
    right: -20px;
    bottom: -20px;
    left: -20px;
    opacity: 0;
  }
}
@keyframes gradient-circle_after_rent{
  0%{
    top: 4px;
    right: 4px;
    bottom: 4px;
    left: 4px;
  }
  100%{
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}

