// Блок "РЕВОЛЮЦИЯ В ОБЛАСТИ ЭПИЛЯЦИИ"
.section-revolution{
  padding: 70px 0 80px;
  background: #f6f6f6;
  @include max(md){
    padding: 50px 0;
  }
  &__bg{
    background: url(../img/background/bg__screen_revolution.jpg) center / cover no-repeat;
    opacity: .1;
    z-index: 2;
  }
  &__overlay{
    z-index: 1;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
      border-radius: 50%;
    }
    &:before{
      left: -505px;
      width: 1123px;
      height: 1123px;
      @include max(sm){
        display: none;
      }
    }
    &:after{
      bottom: -360px;
      right: -295px;
      top: auto;
      transform: translateY(0);
      width: 896px;
      height: 896px;
      @include max(sm){
        right: 0;
      }
    }
  }
}
.revolution{
  &-content{
    width: 50%;
    margin-top: 60px;
    @include max(md){
      width: 100%;
      margin: 30px 0 0;
    }
    .content{
      max-width: 550px;
      padding: 35px 180px 35px 30px;
      background: #fff;
      border-radius: 30px;
      box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.1);
      font-weight: 300;
      font-size: 18px;
      @include max(md){
        max-width: none;
        padding: 30px;
      }
      b, strong{
