    @include max(sm){
      width: calc(100% + 30px);
      margin: 30px -15px 0;
      border-radius: 0;
    }
  }
}
.video{
  &-block{
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
    background: #000;
    border-radius: 30px;
    &__poster {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      display: none;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      opacity: 0.5;
    }
    &__play{
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%) scale(1);
      display: none;
      width: 78px;
      height: 78px;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
      border-radius: 50%;
      z-index: 2;
      cursor: pointer;
      transition: $transition;
      @include max(sm){
        width: 50px;
        height: 50px;
      }
      &:before{
        content: '';
        position: absolute;
        left: 52%;
        top: 50%;
        display: block;
        width: 21px;
        height: 31px;
        margin-left: -10.5px;
        margin-top: -15.5px;
        background: url(../img/play.png) center / contain no-repeat;
        @include max(sm){
          width: 15px;
          height: 22px;
          margin-left: -7.5px;
          margin-top: -10.5px;
        }
      }
      &:after{
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        display: block;
        width: 86px;
        height: 86px;
        background: url(../img/play-after.png) center / contain no-repeat;
        transition: $transition;
        @include max(sm){
          width: 60px;
          height: 60px;
        }
      }
      &:hover{
        transform: translate(-50%,-50%) scale(.9);
        &:after{
          width: 78px;
          height: 78px;
        }
      }
    }
  }
}
.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  padding-bottom: 56.25%;
  overflow: hidden;
  iframe, video{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
}

