*{
  box-sizing: border-box;
  user-select: none;
  &:before, &:after{
    box-sizing: border-box;
  }
}
body {
  position: relative;
  width: 100%;
  max-width: 1920px;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  background-color: #fff;
  cursor: default;
}

.clearfix {
  width: 100%;
  clear: both;
}

.mfp-container{
  overflow: auto;
}
svg{
  pointer-events: all;
}
.container{
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  @include min(lg){
    width: 1200px;
  }
}
.row{
  margin-left: -15px;
  margin-right: -15px;
}
.col{
  padding: 0 15px;
}
.wrapper{
  overflow: hidden;
}
.hidden{
  display: none !important;
}
.baron{
  height: 100%;
  overflow: hidden;
  &._simple{
    .baron__scroller{
      padding-right: 10px;
    }
    .baron__track {
      top: 0;
      bottom: 0;
      right: 0;
      width: 6px;
      border-radius: 0;
      background: #e8e8e8;
    }
    .baron__bar {
      width: 6px;
      border-radius: 0;
      background: $green-light;
    }
    &.baron {
      .baron__bar {
        opacity: 1;
      }
    }
  }
}
.custom-balloon{
  position: absolute;
  top: auto !important;
  bottom: 60px;
  width: 265px;
  border-radius: 10px;
  background: #fff;
  padding: 25px 15px;
  text-align: center;
  filter: drop-shadow(-20px 25px 20px  rgba(0, 0, 0, 0.15));
  &__arrow{
    position: absolute;
    bottom: -15px;
    left: 118.5px;
    width: 30px;
    height: 30px;
    background: #fff;
    transform: rotate(45deg);
  }
  &__close{
    position: absolute;
    top: 8px;
    right: 8px;
    cursor: pointer;
  }
  &__logo{
    img{
      max-width: 100%;
    }
  }
  &__title{
    margin-bottom: 5px;
    font-weight: 700;
    color: $green-dark;
  }
  &__header{
    margin-bottom: 5px;
    font-size: 14px;
    *{
      margin: 0;
    }
  }
  &__body{
    margin-bottom: 5px;
    *{
      margin: 0;
    }
  }
  &__footer{
    font-size: 14px;
    *{
      margin: 0;
    }
  }
  ymaps{
    width: calc(265px - 30px) !important;
  }
  img{
    max-width: 100%;
  }
}