// Модальные
.modal {
  position: relative;
  max-width: 432px;
  margin: 0 auto;
  padding: 30px 30px 40px;
  background: #fff;
  border-radius: 30px;
  box-shadow: 0px 0px 48px 0px rgba(0, 0, 0, 0.21);
  
  @include max(md){
    max-width: 400px;
    padding: 30px 50px 50px;
  }
  @include max(sm){
    width: calc(100% - 30px);
    margin: 0 auto;
    padding: 20px 30px 30px;
    border-radius: 10px;
  }
  @include max(xs){
    padding: 20px 15px 30px;
  }
  &-close{
    position: absolute;
    right: -42px;
    top: -5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    margin: 0;
    padding: 0;
    background: transparent;
    border: 0;
    border-radius: 50%;
    outline: 0;
    // box-shadow: 0 14px 24px rgba(0,0,0,.17);
    @include max(sm){
      right: 2px;
      top: 2px;
      width: 20px;
      height: 20px;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
    }
    img{
      display: block;
      width: 18px;
      @include max(sm){
        width: 10px;
      }
    }
    &:hover{
      opacity: .5;
      box-shadow: none;
    }
  }
  .mfp-close{
    display: none;
  }
  .form{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
}
#modal-policy{
  width: 90%;
  max-width: 1200px;
  margin: 50px auto;
}