.header {
  position: relative;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  padding: 5px 0;
  background: #fff;
  transition: $transition;
  z-index: 10;
  @include min(md){
    padding: 6px 0 8px;
  }
  .row{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__logo{
    width: 25%;
    @include max(md){
      width: 230px;
    }
  }
  &__social{
    margin-bottom: 11px;
    @include min(md){
      &.hidden_md{
        display: block;
      }
      &.visible_md{
        display: none;
      }
    }
    @include max(md){
      &.hidden_md{
        display: none;
      }
      &.visible_md{
        display: block;
      }
      margin-bottom: 15px;
      .social{
        justify-content: center;
      }
    }
  }
  &__links{
    margin-bottom: 5px;
    @include min(md){
      margin-bottom: 13px;
    }
    @include min(sm){
      &.hidden_sm{
        display: block;
      }
      &.visible_sm{
        display: none;
      }
    }
    @include max(sm){
      &.hidden_sm{
        display: none;
      }
      &.visible_sm{
        display: block;
      }
      margin-bottom: 15px;
      text-align: center;
    }
  }
  &__call{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5px;
    @include min(md){
      margin-bottom: 13px;
    }
    @include min(xs){
      &.hidden_xs{
        display: flex;
      }
      &.visible_xs{
        display: none;
      }
    }
    @include max(xs){
      &.hidden_xs{
        display: none;
      }
      &.visible_xs{
        display: flex;
      }
      @include max(xs){
        margin-bottom: 0;
      }
    }
    a:not(.button){
      line-height: 16px;
      color: $black;
      text-decoration: none;
      @include min(md){
        b, strong{
          font-weight: 900;
          font-size: 20px;
          line-height: 16px;
        }
      }
      &:hover{
        color: $green-light;
      }
    }
    .button{
      margin-top: 5px;
      @include min(md){
        margin-top: 15px;
      }
      @include max(xs){
        margin-top: 15px;
      }
    }
  }
  &.scroll {
    box-shadow: 0 0 27px rgba(0,0,0,.17);
  }
}

.logo {
  position: relative;
  display: flex;
  align-items: center;
  &:hover{
    opacity: .5;
  }
  img{
    max-width: 100%;
  }
}
.social{
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  a{
    margin: 4px;
    &:hover{
      opacity: .5;
    }
    img{
      display: block;
    }
  }
}
.nav{
  &-toggle {
    position: relative;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    float: right;
    width: 30px;
    height: 30px;
    margin: 0 15px;
    padding: 0;
    border: none;
    outline: none;
    background: transparent;
    @include max(md) {
      display: flex;
      align-self: center;
    }

    span {
      position: absolute;
      display: block;
      width: 22px;
      height: 2px;
      background: $green-dark;
      transition: all .5s ease;

      &._top {
        top: 6px;
      }

      &._mid {
        top: 14px;
        width: 16px;
      }

      &._bot {
        top: 22px;
      }
    }

    &.active {
      span {
        &._top {
          top: 13px;
          transform: rotate(45deg);
        }

        &._mid {
          transform: translateX(-50px);
          opacity: 0;
          transition-delay: .1s;
        }

        &._bot {
          top: 13px;
          transform: rotate(-45deg);
        }
      }
    }
  }
  &-bar{
    display: none;
    width: 100%;
    @include max(md) {
      position: fixed;
      top: 81px;
      bottom: 0;
      left: 0;
      width: 100% !important;
      //height: 100vh;
      margin: 0;
      padding: 50px 0;
      background: rgba(255,255,255,.9);
      overflow: auto;
    }
  }
}