@mixin min($point) {
	@if $point == lg {
		@media (min-width: 1200px) { @content; }
	}
	@if $point == md {
		@media (min-width: 992px) { @content; }
	}
	@if $point == sm {
		@media (min-width: 768px ) { @content; }
	}
	@if $point == xs {
		@media (min-width: 480px ) { @content; }
	}
}
@mixin min-size($point-size) {
	@media (min-width: $point-size) { @content; }
}

@mixin max($point) {
	@if $point == lg {
		@media (max-width: 1199px) { @content; }
	}
	@if $point == md {
		@media (max-width: 991px) { @content; }
	}
	@if $point == sm {
		@media (max-width: 767px ) { @content; }
	}
	@if $point == xs {
		@media (max-width: 479px ) { @content; }
	}
}
@mixin max-size($point-size) {
	@media (max-width: $point-size) { @content; }
}
