.button{
  position: relative;
  display: inline-flex;
  justify-content: center;
  padding: 9px 40px 10px;
  background: linear-gradient( -90deg, $green-light 0%, $green-dark 100%);
  border: none;
  border-radius: 25px;
  box-shadow: 0 0 0 rgba(0,78,73,0);
  color: #fff;
  text-align: center;
  text-decoration: none;
  z-index: 1;
  overflow: hidden;
  @include max(lg){
    padding: 9px 30px 10px;
  }
  @include max(md){
    padding: 9px 20px 10px;
  }
  &__gradient{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
    border-radius: inherit;
    transition: $transition;
  }
  &__text{
    position: relative;
    z-index: 2;
  }
  &:hover{
    box-shadow: 0 0 15px rgba(0,78,73,.31);
    .button__gradient{
      opacity: 0;
    }
  }
  &_gradient{
    font-weight: 500;
    .button__gradient{
      left: 1px;
      right: 1px;
      top: 1px;
      bottom: 1px;
      background: #fff;
    }
    .button__text{
      display: flex;
      align-items: center;
      background: linear-gradient( -90deg, $green-light 0%, $green-dark 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &:hover,
    &.active{
      .button__gradient{
        opacity: 0;
      }
      .button__text{
        background: #fff;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
  &_white{
    background: #fff;
    font-weight: 500;
    .button__gradient{
      display: none;
    }
    .button__text{
      display: inline-block;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &:hover{
      box-shadow: 0 0 15px rgba(255,255,255,.6);
    }
  }
}
// Кнопка вверх
.back-top {
  position: fixed;
  bottom: 12.6vh;
  right: 4.6vw;
  display: none;
  z-index: 5;
  @include max(lg) {
    right: 5px;
    bottom: 30px;
  }
  &__arrow{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin: 0 auto;
    border-radius: 50%;
    background: $green-light;
    &:before{
      content: '';
      position: absolute;
      left: 2px;
      right: 2px;
      top: 2px;
      bottom: 2px;
      border: 3px solid #fff;
      border-radius: inherit;
      @include max(lg) {
        border-width: 2px;
      }
    }
    svg{
      width: 20px;
      polygon{
        fill: #fff;
      }
    }
  }
  // &__text{
  //   margin-top: 5px;
  //   @include max(lg) {
  //     display: none;
  //   }
  // }
  a {
    display: block;
    padding: 0;
    transition: 1s;
    border-radius: 50%;
    background: transparent;
    box-shadow: 0 14px 24px rgba(0,0,0,.17);
    color: #722d12;
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;
    &:hover {
      box-shadow: none;
    }
  }
}