// Типографика
body{
  font-family: $font-futura;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: $black;
}
h1, h2, h3, h4, h5, h6,
.title{
  width: 100%;
  // margin: 0 0 10px;
  margin: 0;
  font-weight: 700;
  span{
    color: $green-dark;
  }
}
h1, .title{
  font-size: 50px;
  @include max(lg){
    font-size: 40px;
  }
  @include max(md){
    font-size: 34px;
  }
}
h2, .title_xl{
  font-size: 40px;
  @include max(lg){
    font-size: 35px;
  }
  @include max(md){
    font-size: 30px;
  }
}
h3, .title_lg{
  font-size: 35px;
  @include max(lg){
    font-size: 30px;
  }
  @include max(md){
    font-size: 26px;
  }
}
h4, .title_md{
  font-size: 30px;
  @include max(lg){
    font-size: 25px;
  }
  @include max(md){
    font-size: 22px;
  }
}
h5, .title_sm{
  font-size: 25px;
  @include max(lg){
    font-size: 22px;
  }
  @include max(md){
    font-size: 20px;
  }
}
h6, .title_xs{
  font-size: 18px;
}

hr {
  margin: 25px 0;
  border: none;
  border-top: 1px solid $green-dark;
  @include max(md){
    margin: 15px 0;
  }
}

ul, ol{
  margin: 0 0 10px;
  padding-left: 40px;
  &.list-style{
    margin: 0 0 10px;
    padding: 0;
    list-style: none;
    list-style-type: none;
  }
}
ul{
  list-style: none;
  li{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: -15px;
      top: 6px;
      display: inline-block;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      background: $green-dark;
    }
  }
}

p{
  // margin: 0 0 10px;
  margin: 0;
}

b, strong {
  font-weight: 700;
}

a, button {
  outline: none;
  box-shadow: none;
  cursor: pointer;
  transition: $transition;
}
a{
  color: $green-light;
  text-decoration: underline;
  &:hover{
    text-decoration: none;
  }
}

table{
  min-width: 100%;
  margin: 0;
  border-spacing: 8px 5px;
  border-radius: 10px;
  @include max(md){
    border-spacing: 5px 3px;
  }
  tr{
    th{
      padding: 13px 26px;
      background: #fff;
      border-radius: 10px;
      box-shadow: 0 0 50px 0 rgba(0,0,0,.1);
      font-weight: 500;
      color: #004e49;
      text-transform: uppercase;
      @include max(md){
        box-shadow: none;
      }
    }
    td{
      padding: 13px 26px;
      background: #fff;
      border-radius: 10px;
      box-shadow: 0 0 50px 0 rgba(0,0,0,.1);
      color: $black;
      @include max(md){
        box-shadow: none;
      }
      b, strong{
        font-weight: 500;
      }
    }
  }
}
.table{
  &-responsive{
    width: 100%;
    @include max(md){
      overflow: auto;
      table{
        white-space: nowrap;
      }
    }
  }
}

._invert{
  color: $text-color-light;
}
.text{
  &_left{
    text-align: left;
  }
  &_center{
    text-align: center;
  }
  &_right{
    text-align: right;
  }
  &_lowercase{
    text-transform: lowercase;
  }
  &_uppercase{
    text-transform: uppercase;
  }
  &_capitalize{
    text-transform: capitalize;
  }
}
.content{
  width: 100%;
  img{
    max-width: 100% !important;
    height: auto !important;
    border-radius: $radius;
  }
}