.slider {
  &__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex !important;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 41px;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0;
    box-shadow: none;
    cursor: pointer;
    z-index: 1;
    transition: all $transition;
    overflow: hidden;
    // path{
    //   fill: #fff;
    //   transition: inherit;
    // }

    // img{
    //   position: relative;
    //   max-width: 80%;
    // }

    &[aria-disabled="true"] {
      opacity: 0;
      pointer-events: none;
    }

    &_prev {
      left: 15px;
      background-image: url(../img/arrow_prev.png);
    }

    &_next {
      right: 15px;
      background-image: url(../img/arrow_next.png);
    }

    &:hover {
      opacity: .5;
      // path{
      //   fill: #722d12;
      // }
    }
  }
}
.slick-loading .slick-list{
  background: #fff url('../img/libs/ajax-loader.gif') center center no-repeat;
}
.slick-dots {
  position: relative;
  display: flex !important;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  text-align: center;
  line-height: 0;
  list-style: none;

  li {
    position: relative;
    width: 16px;
    height: 16px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    &:before{
      display: none;
    }

    button {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border: 0;
      background: linear-gradient( 90deg, $green-light 0%, $green-dark 100%);
      border-radius: 50%;
      display: block;
      width: 100%;
      height: 100%;
      outline: none;
      line-height: 0px;
      font-size: 0px;
      color: transparent;
      padding: 0;
      cursor: pointer;
      transition: none;
      &:before {
        content: '';
        position: absolute;
        top: 2px;
        left: 2px;
        display: block;
        width: 12px;
        height: 12px;
        background: #fff;
        border-radius: 50%;
        opacity: 1;
        transition: $transition;
        // display: none;
        // font-family: Ubuntu;
        // font-size: 6px;
        // line-height: 20px;
        // position: absolute;
        // top: -8px;
        // left: 0;
        // width: 100%;
        // height: 100%;
        // content: "•";
        // text-align: center;
        // opacity: .25;
        // color: #000;
        // -webkit-font-smoothing: antialiased;
        // -moz-osx-font-smoothing: grayscale;
      }
    }

    &:hover {
      button {
        // background: #129bfa;
        // border-color: $green-light;
        &:before {
          opacity: .75;
        }
      }
    }

    &.slick-active {
      button {
        // background: #A55A73;
        // border-color: $green-light;
        &:before {
          opacity: 0;
        }
      }
    }
  }
}
.slick-slide{
  outline: none;
  img{
    display: block;
    max-width: 100%;
  }
}
.slick-dotted.slick-slider {
  margin-bottom: 0;
}